import React, { useState, useEffect, forwardRef, useImperativeHandle } from 'react';
import { FlatList } from 'react-native';
import { emptyDataView, activityIndicatorView, noMoreDataView } from './View';
const QuickList = (props, ref) => {
    const { onFetch, // ËØ∑Ê±ÇÁöÑÊé•Âè£ Promise Á±ªÂûã
    data, // Êï∞ÊçÆÊ∫ê Array Á±ªÂûã
    renderItem = null, // Ê∏≤ÊüìÊñπÊ≥ï Function Á±ªÂûã
    keyId, // ÂàóË°®ÂîØ‰∏Ä key
    emptyView = emptyDataView, pageSize = 10, // ÊØèÊ¨°Âä†ËΩΩÊï∞ÊçÆÊù°Êï∞
    total = 0, // ÊÄªÊù°Êï∞
    style, ...others } = props;
    const PaginationStatus = {
        firstLoad: 0, // È¶ñÊ¨°Âä†ËΩΩ
        waiting: 1, // Á≠âÂæÖ‰∏≠
        allLoaded: 2, // ÂÖ®ÈÉ®Âä†ËΩΩÂÆåÊàê
    };
    // ÊåÇËΩΩÁä∂ÊÄÅ
    let mounted = true;
    const [endReachedTime, setEndReachedTime] = useState(Date.now());
    // È¶ñÊ¨°Âä†ËΩΩÊ†áËØÜ
    const [firstLoader, setFirstLoader] = useState(true);
    // ‰∏äÊãâÂä†ËΩΩ loading
    const [loading, setLoading] = useState(false);
    // ‰∏ãÊãâÂà∑Êñ∞ refreshing
    const [refreshing, setRefreshing] = useState(false);
    // È°µÊï∞ page number
    const [page, setPage] = useState(1);
    // ÂàùÂßãÈ°µÈù¢Áä∂ÊÄÅ
    const [paginationStatus, setPaginationStatus] = useState(PaginationStatus.firstLoad);
    // Êö¥Èú≤ÁªôÁà∂ÁªÑ‰ª∂Ë∞ÉÁî®ÁöÑÊñπÊ≥ï
    useImperativeHandle(ref, () => ({
        executeRefreshAction: async (param = {}) => {
            setPage(1);
            const params = {
                page: 1,
                pageSize,
                ...param,
            };
            onFetch && (await onFetch(params));
        },
    }));
    useEffect(() => {
        // ÊåÇËΩΩÂàóË°®
        mounted = true;
        // ÊåÇËΩΩÂÆåÊàêÈ¶ñÊ¨°ÊâßË°åÂä†ËΩΩ
        firstLoader &&
            firstLoad().catch((error) => {
                console.error(error);
            });
        return () => {
            // Âç∏ËΩΩÂàóË°®
            mounted = false;
        };
    }, [firstLoader]);
    useEffect(() => {
        // È¶ñÊ¨°Âä†ËΩΩÂíåÊÄªÈáè‰∏∫0Êó∂ÂÄôÁõ¥Êé•ËøîÂõû
        if (firstLoader || total === 0) {
            return;
        }
        let maxPage = Math.ceil(total / pageSize);
        if (maxPage === page) {
            setPaginationStatus(PaginationStatus.allLoaded);
        }
        else {
            setPaginationStatus(PaginationStatus.waiting);
        }
    }, [page, total, firstLoader, pageSize]);
    const renderFooter = () => {
        if (total === 0) {
            return null;
        }
        else if (paginationStatus === PaginationStatus.waiting) {
            return activityIndicatorView;
        }
        else if (!loading && paginationStatus === PaginationStatus.allLoaded && total !== 0) {
            return noMoreDataView;
        }
    };
    const renderEmptyView = () => {
        if (!firstLoader && typeof emptyView === 'function') {
            return emptyView();
        }
        return null;
    };
    // È¶ñÊ¨°Âä†ËΩΩ
    const firstLoad = async () => {
        if (paginationStatus === PaginationStatus.firstLoad) {
            setPage(1);
            setRefreshing(true);
            const params = {
                page: 1,
                pageSize,
                isRefresh: true,
            };
            await onFetch(params);
            setRefreshing(false);
            setFirstLoader(false);
        }
    };
    // ‰∏ãÊãâÂà∑Êñ∞ÂàóË°®
    const onRefresh = async () => {
        if (mounted && !loading) {
            setPage(1);
            setRefreshing(true);
            const params = {
                page: 1,
                pageSize,
                isRefresh: true,
            };
            await onFetch(params);
            setRefreshing(false);
        }
    };
    // ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
    const onEndReached = async () => {
        if (paginationStatus === PaginationStatus.firstLoad ||
            paginationStatus === PaginationStatus.allLoaded ||
            refreshing ||
            loading) {
            return;
        }
        setLoading(true);
        const params = {
            page: page + 1,
            pageSize,
            isRefresh: false,
        };
        await onFetch(params);
        setLoading(false);
        setPage(page + 1);
    };
    return (<FlatList {...others} ref={ref} onEndReachedThreshold={0.1} data={data} renderItem={renderItem} refreshing={refreshing} extraData={data} keyExtractor={(item) => item[keyId]} initialNumToRender={pageSize} onRefresh={onRefresh} onEndReached={() => {
            const now = Date.now();
            if (now - endReachedTime > 1000) {
                onEndReached && onEndReached();
                setEndReachedTime(now);
            }
        }} ListFooterComponent={renderFooter()} ListEmptyComponent={renderEmptyView()} style={style}/>);
};
export default forwardRef(QuickList);
